<template>
  <section class="form" id="contactUs">
    <div class="container">
      <div class="row">
        <div class="col">
          <h2 class="form__heading">Contact Us</h2>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-md-6 form__col form__col--left">
          <form @submit.prevent="onSubmit">
            <div class="form-group" :class="{'is-invalid': errors.first('name')}">
              <input
                type="text"
                class="form-control"
                id="name"
                autocomplete="off"
                v-model="name"
                name="name"
                v-validate="'required'"
              >
              <label for="name" class="form-group__label">Name</label>
              <span class="invalid-text">{{ errors.first('name') }}</span>
            </div>
            <div class="form-group" :class="{'is-invalid': errors.first('phone')}">
              <input
                type="tel"
                class="form-control"
                id="phone"
                autocomplete="off"
                v-model="phone"
                name="phone"
                v-validate="'required|numeric'"
              >
              <label for="name" class="form-group__label">Phone</label>
              <span class="invalid-text">{{ errors.first('phone') }}</span>
            </div>
            <div class="form-group" :class="{'is-invalid': errors.first('email')}">
              <input
                type="email"
                class="form-control"
                id="email"
                autocomplete="off"
                v-model="email"
                name="email"
                v-validate="'required|email'"
                data-vv-validate-on="blur"
              >
              <label for="name" class="form-group__label">E-mail</label>
              <span class="invalid-text">{{ errors.first('email') }}</span>
            </div>
            <div class="form-group check">
              <input
                type="checkbox"
                class="form-check-input visually-hidden"
                id="check"
                v-model="check"
                name="check"
                v-validate="'required'"
              >
              <label for="check" class="check__label">I agree the processing of personal data</label>
            </div>
            <div class="form-group form-group--btn">
              <button
                type="submit"
                class="form__btn"
                :disabled="!isFormValid || loading"
              >Get in touch</button>
            </div>
          </form>
        </div>
        <div class="col-md-6 form__col form__col--right">
          <p
            class="form__text"
          >Please tell us more about your request and give us info about your company and country</p>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      email: "",
      phone: "",
      check: false
    };
  },
  computed: {
    isFormValid() {
      return !Object.keys(this.fields).some(key => this.fields[key].invalid);
    },
    loading() {
      return this.$store.state.loading;
    }
  },
  methods: {
    onSubmit() {
      let formData = new FormData();
      formData.append("name", this.name);
      formData.append("email", this.email);
      formData.append("phone", this.phone);

      this.$store.dispatch("fetchData", formData);
    }
  }
};
</script>

<style lang="scss">
@import "../scss/variables.scss";
.form {
  padding: 30px 0;
  background: #00b660
    url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAALQAAACgCAMAAACSVLaNAAAAHlBMVEUAt1oAvGYAvGcAvWcAwHEAw3gAxHoAx4EAyIMAyYaf6l1kAAAAYUlEQVR42u3UuwnAMBBEwZOs3/XfsMEoc2ywYKaCFywbAAAAAAAAAAAAf1L6nL3EUVo+Whyk5lbjHD23EeeYuS3R5vFScqsu72vXWGvUAAAAAAAAAAAAAAAAAAAAAADgUzfePwKjLM4fYwAAAABJRU5ErkJggg==);
  background-size: 90px;
  @media (min-width: 992px) {
    padding: 90px 0;
  }
}

.form-group {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 13px;
  .form-control {
    width: 100%;
    border: 0;
    border-bottom: 1px solid #000;
    background: transparent;
    padding: 10px 0;
    padding-left: 90px;
    font-size: 18px;
    &:focus {
      outline: none;
      border-bottom: 1px solid #fff;
      color: #fff;
    }
    &:focus ~ label {
      color: #fff;
    }
  }
}

.form-group__label {
  width: 80px;
  margin-bottom: 0;
  position: absolute;
  left: 0;
  top: 50%;
  font-size: 18px;
  transform: translateY(-50%);
}

.form__heading {
  color: #fff;
  margin-bottom: 24px;
}

.check__label {
  position: relative;
  padding-left: 35px;
  margin-bottom: 0;
  font-size: 18px;
  letter-spacing: -0.4px;
  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 6px;
    width: 15px;
    height: 15px;
    background: #fff;
  }
}

.form-check-input:checked ~ label {
  &::after {
    content: "";
    position: absolute;
    left: 1px;
    top: 6px;
    width: 15px;
    height: 15px;
    background-image: url(data:image/png;base64,);
  }
}

.form__text {
  font-size: 18px;
  line-height: 34px;
}

.form__col--left {
  @media (min-width: 768px) {
    padding-right: 40px;
  }
}
.form__col--right {
  @media (min-width: 768px) {
    padding-left: 40px;
  }
}

.form__btn {
  background: #000
    url(data:image/png;base64,);
  padding: 33px 94px;
  border: 0;
  box-shadow: none;
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  color: #fff;
  display: inline-block;
  &:hover {
    cursor: pointer;
    background: #000;
  }
  &:disabled {
    cursor: default;
    opacity: 0.7;
  }
  &:disabled:hover {
    background: #000
      url(data:image/png;base64,);
    cursor: default;
    opacity: 0.7;
  }
}

.form-group--btn {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  @media (min-width: 768px) {
    display: block;
    margin-bottom: 0;
  }
}

.check {
  margin-bottom: 45px;
}

.is-invalid {
  input.form-control {
    border-color: $error;
    &:focus {
      border-color: $error;
    }
  }
  .form-group__label {
    color: $error;
  }
}

.invalid-text {
  position: absolute;
  top: 100%;
  color: $error;
  display: inline-block;
  font-size: 12px;
}
</style>
