<template>
  <section class="map">
    <div class="container map__container">
      <div class="row">
        <div class="col-lg-6">
          <h2 class="map__heading">Our Offices</h2>

          <ul class="nav nav-tabs" role="tablist">
            <li class="nav-item" v-for="(item, index) in tabsData" :key="index">
              <a
                class="nav-link"
                id="home-tab"
                data-toggle="tab"
                href="javascript:void(0);"
                role="tab"
                :class="{active: activeTab === index + 1}"
                @click="activeTab = index + 1, setcenter(index + 1)"
              >{{item.name}}</a>
            </li>
          </ul>
          <div class="tab-content">
            <div
              v-for="(item, index) in tabsData"
              :key="index"
              class="tab-pane fade"
              role="tabpanel"
              :class="{active: activeTab === index + 1, show: activeTab === index + 1}"
            >
              <h3 class="map__subheading">{{item.heading}}</h3>
              <address class="map__address">
                <p>{{item.address}}</p>
                <p>{{item.num}}</p>
                <p>{{item.country}}</p>
              </address>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="map-wrapper">
      <gmap-map
        ref="mapRef"
        class="test"
        :center="center"
        :zoom="12"
        style="width:100%;  height: 500px;"
        :options="{
        zoomControl: false,
        mapTypeControl: false,
        scaleControl: false,
        streetViewControl: false,
        rotateControl: false,
        fullscreenControl: false,
        disableDefaultUi: false,
        styles: styles
      }"
      >
        <gmap-marker
          :key="index"
          v-for="(marker, index) in markers"
          :position="marker.position"
          :icon="{ url: require('../assets/marker.svg')}"
          @click="center=marker.position"
        ></gmap-marker>
      </gmap-map>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      activeTab: 1,
      tabsData: [
        {
          name: "Kyiv",
          heading: "Global Message Services Ukraine LLC",
          address: "Kuiv, Stepan Bandera, 33",
          num: "02066",
          country: "Ukraine"
        },
        {
          name: "New york",
          heading: "Global Message Services New york LLC",
          address: "New york, Stepan Bandera, 33",
          num: "02067",
          country: "USA"
        },
        {
          name: "Guangzhou",
          heading: "Global Message Services Guangzhou LLC",
          address: "Guangzhou, Stepan Bandera, 33",
          num: "02068",
          country: "China"
        },
        {
          name: "Barcelona",
          heading: "Global Message Services Barcelona LLC",
          address: "Barcelona, Stepan Bandera, 33",
          num: "02069",
          country: "Italy"
        }
      ],
      center: { lat: 50.417074, lng: 30.523888 },
      styles: [
        {
          elementType: "geometry",
          stylers: [
            {
              color: "#212121"
            }
          ]
        },
        {
          elementType: "labels.icon",
          stylers: [
            {
              visibility: "off"
            }
          ]
        },
        {
          elementType: "labels.text.fill",
          stylers: [
            {
              color: "#757575"
            }
          ]
        },
        {
          elementType: "labels.text.stroke",
          stylers: [
            {
              color: "#212121"
            }
          ]
        },
        {
          featureType: "administrative",
          elementType: "geometry",
          stylers: [
            {
              color: "#757575"
            }
          ]
        },
        {
          featureType: "administrative.country",
          elementType: "labels.text.fill",
          stylers: [
            {
              color: "#9e9e9e"
            }
          ]
        },
        {
          featureType: "administrative.land_parcel",
          stylers: [
            {
              visibility: "off"
            }
          ]
        },
        {
          featureType: "administrative.locality",
          elementType: "labels.text.fill",
          stylers: [
            {
              color: "#bdbdbd"
            }
          ]
        },
        {
          featureType: "poi",
          elementType: "labels.text.fill",
          stylers: [
            {
              color: "#757575"
            }
          ]
        },
        {
          featureType: "poi.park",
          elementType: "geometry",
          stylers: [
            {
              color: "#181818"
            }
          ]
        },
        {
          featureType: "poi.park",
          elementType: "labels.text.fill",
          stylers: [
            {
              color: "#616161"
            }
          ]
        },
        {
          featureType: "poi.park",
          elementType: "labels.text.stroke",
          stylers: [
            {
              color: "#1b1b1b"
            }
          ]
        },
        {
          featureType: "road",
          elementType: "geometry.fill",
          stylers: [
            {
              color: "#2c2c2c"
            }
          ]
        },
        {
          featureType: "road",
          elementType: "labels.text.fill",
          stylers: [
            {
              color: "#8a8a8a"
            }
          ]
        },
        {
          featureType: "road.arterial",
          elementType: "geometry",
          stylers: [
            {
              color: "#373737"
            }
          ]
        },
        {
          featureType: "road.highway",
          elementType: "geometry",
          stylers: [
            {
              color: "#3c3c3c"
            }
          ]
        },
        {
          featureType: "road.highway.controlled_access",
          elementType: "geometry",
          stylers: [
            {
              color: "#4e4e4e"
            }
          ]
        },
        {
          featureType: "road.local",
          elementType: "labels.text.fill",
          stylers: [
            {
              color: "#616161"
            }
          ]
        },
        {
          featureType: "transit",
          elementType: "labels.text.fill",
          stylers: [
            {
              color: "#757575"
            }
          ]
        },
        {
          featureType: "water",
          elementType: "geometry",
          stylers: [
            {
              color: "#000000"
            }
          ]
        },
        {
          featureType: "water",
          elementType: "labels.text.fill",
          stylers: [
            {
              color: "#3d3d3d"
            }
          ]
        }
      ],
      markers: [
        {
          position: {
            lat: 50.417074,
            lng: 30.523888
          }
        },
        {
          position: {
            lat: 40.718958,
            lng: -73.996695
          }
        },
        {
          position: {
            lat: 23.133899,
            lng: 113.252588
          }
        },
        {
          position: {
            lat: 41.376704,
            lng: 2.169
          }
        }
      ],
      places: [],
      currentPlace: null
    };
  },
  methods: {
    setcenter(num) {
      const lat = this.markers[num - 1].position.lat;
      const lng = this.markers[num - 1].position.lng;
      this.$refs.mapRef.$mapPromise.then(map => {
        map.panTo({ lat: lat, lng: lng });
      });
    }
  },
  mounted() {
    this.setcenter(1);
  }
};
</script>

<style lang="scss">
@import "../scss/variables.scss";
.map {
  padding-top: 50px;
  background: #000
    url(data:image/png;base64,);
  position: relative;
  color: #fff;
  display: flex;
  flex-direction: column;
  @media (min-width: 992px) {
    display: block;
    height: 500px;
    padding-top: 90px;
  }
}

.map__container {
  padding-bottom: 50px;
  @media (min-width: 992px) {
    padding-bottom: 0;
  }
}

.map__heading {
  margin-bottom: 32px;
}

.map__subheading {
  font-family: $fontLight;
  font-size: 26px;
  line-height: 30px;
  letter-spacing: 0.7px;
  margin-bottom: 37px;
}

.map__address {
  p {
    font-family: $fontLight;
    font-size: 11px;
    line-height: 14px;
    letter-spacing: 0.5px;
    margin-bottom: 7px;
  }
}

.nav-link {
  padding: 0;
  font-family: $fontBold;
  font-size: 11px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
}

.nav-tabs .nav-link {
  border: 0;
  color: #fff;

  &:hover {
    color: $green;
  }
}

.nav-tabs .nav-link.active,
.nav-tabs .nav-item.show .nav-link {
  color: $green;
  background-color: transparent;
}

.nav-item:not(:last-child) {
  margin-right: 20px;
}

.nav.nav-tabs {
  border: 0;
  margin-bottom: 50px;
}

.map-wrapper {
  height: 500px;
  @media (min-width: 992px) {
    position: absolute;
    width: 50%;
  }
  width: 100%;
  right: 0;
  top: 0;
}
</style>
